
import './App.css';

import React, { useState, Suspense } from 'react';

const LevelOne = React.lazy(() => import('./Nivel 1/LevelOne'));
const Inicio = React.lazy(() => import('./inicio/inicio'));

function App() {

  const [start, setStart] = useState(false);

  return (
    <Suspense fallback={<div className="load">

        <p>Cargando...</p>

      </div>}>
          <div className="App">

            {start? <LevelOne/>:<Inicio setStart={setStart}/>}
            
          </div>
    </Suspense>
  );
}

export default App;
